.article-blog {
	// margin-bottom: toRem(80);
	&__part {
		@include adaptiveValue("margin-bottom", 50, 30, 0, 1360, 992);
		@media (max-width: toEm(380)) {
			margin-bottom: toRem(20);
			&:has(.article-blog__list-wrap) {
				margin-bottom: toRem(30);
			}
			&_text {
				margin-bottom: toRem(30);
			}
		}
		&_ol {
			ol {
				counter-reset: my-counter;
			}
			margin-top: toRem(60);
			padding: 2.5rem;
			border: 0.0625rem dashed $color-seryy-01-1;
			border-radius: 0.75rem;
			@media (max-width: $tablet) {
				margin-top: toRem(50);
			}
			@media (max-width: $mobile) {
				padding: 1.875rem;
				margin-top: toRem(40);
			}
			@media (max-width: $mobileSmall) {
				margin-top: toRem(30);
				padding: 1.25rem;
			}
			.article-blog__subsubtitle {
				margin-bottom: toRem(30);
				@media (max-width: $mobileSmall) {
					margin-bottom: toRem(20);
				}
			}
		}
		&_table {
			margin-top: toRem(100);
			@include adaptiveValue("margin-top", 100, 60, 0, 1360, 768);
			@media (max-width: 23.75rem) {
				margin-bottom: toRem(50);
			}
			@media (max-width: $mobile) {
				margin-right: -2.0625rem;
			}
			@media (max-width: $mobileSmall) {
				margin-top: toRem(50);
			}
			.blogs__title {
				margin-bottom: toRem(60);
				@media (max-width: $mobile) {
					margin-bottom: toRem(50);
				}
			}
		}
	}

	&__text {
		line-height: 1.8;
		&:not(:last-child) {
			margin-bottom: toRem(20);
			@media (max-width: $mobileSmall) {
				margin-bottom: toRem(15);
			}
		}
		@media (max-width: $mobileXSmall) {
			font-size: toRem(15);
		}
	}

	&__subtitle {
		font-weight: 500;
		font-size: toRem(24);
		line-height: 1.5;
		font-family: "Unbounded-medium";
		color: $color-zagolovki;
		margin-bottom: toRem(15);
		@media (max-width: $mobileSmall) {
			font-size: toRem(20);
		}
	}

	&__footnote {
		display: flex;
		background: $color-belyy;
		border: 0.0625rem dashed $color-seryy-01-1;
		border-radius: 0.75rem;
		padding: 1.5625rem 1.875rem 1.875rem 1.875rem;
		gap: toRem(30);
		@media (max-width: toEm(700)) {
			flex-direction: column;
		}
		@media (max-width: 24.0625rem) {
			padding: 1.25rem;
		}
	}

	&__img {
		flex: 0 0 10rem;
		position: relative;
		@media (max-width: toEm(700)) {
			@include adaptiveValue("flex-basis", 240, 200, 0, 576, 380);
		}
	}

	&__footnote-img {
		border-radius: 0.5rem;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	&__note {
		.article-blog__subsubtitle {
			@media (max-width: $mobileSmall) {
				font-size: toRem(16);
				margin-bottom: toRem(10);
			}
		}
	}

	&__quote {
		margin-block: toRem(70);
		@media (max-width: $pc) {
			margin-block: toRem(60);
		}
		@include adaptiveValue("margin-block", 40, 30, 0, 992, 380);
		.post__header {
			margin-bottom: toRem(0);
		}
		.form-add__description {
			margin-bottom: toRem(20);
		}
		.post__img {
			width: 2.75rem;
		}
		.post__role {
			font-size: toRem(13);
		}
	}

	&__subsubtitle {
		@extend .article-blog__subtitle;
		font-size: toRem(18);
	}

	&__list-wrap {
		padding: 2.5rem;
		border: 0.0625rem dashed $color-seryy-01-1;
		border-radius: 0.75rem;
		@media (max-width: toEm(700)) {
			padding: 1.875rem;
		}
		@media (max-width: $mobileSmall) {
			padding: 1.25rem;
		}
		.article-blog__subsubtitle {
			font-size: toRem(20);
			margin-bottom: toRem(30);
			@media (max-width: $mobileSmall) {
				margin-bottom: toRem(20);
			}
		}
	}
	&__list {
		column-count: 2;
		column-gap: toRem(60);
		@media (max-width: toEm(870)) {
			column-count: 1;
		}
		&_ol {
			column-gap: toRem(80);
		}
	}

	&__item {
		position: relative;
		display: flex;
		align-items: center;
		padding-left: toRem(34);
		ol & {
			align-items: flex-start;
			padding-left: toRem(39);
		}
		ul &::before {
			content: "";
			position: absolute;
			top: 0.6875rem;
			// transform: translateY(-50%);
			left: 0.75rem;
			width: toRem(6);
			height: toRem(6);
			background-color: $color-zelenyy-01;
			border-radius: 50%;
		}
		ol &::before {
			counter-increment: my-counter;
			content: counter(my-counter);
			width: toRem(24);
			height: toRem(24);
			background: linear-gradient(132deg, #0dbd86 0%, #099a75 99.06%);
			border-radius: 50%;
			position: absolute;
			left: 0;
			display: grid;
			place-items: center;
			font-family: "IBM-semibold";
			color: $color-belyy;
			font-size: toRem(12);
			line-height: 1.83;
		}
		&:not(:last-child) {
			margin-bottom: toRem(20);
			@media (max-width: toEm(576)) {
				margin-bottom: toRem(15);
			}
			@media (max-width: $mobileSmall) {
				margin-bottom: toRem(10);
			}
		}
	}
}
.article-blog__table {
	width: 100%;
	border-collapse: collapse;
	@media (max-width: 47.9375rem) {
		overflow: auto;
		padding-bottom: toRem(20);
		&::-webkit-scrollbar {
			height: 0.375rem;
		}
		
		&::-webkit-scrollbar-thumb {
			background: $color-zelenyy-02;
			border-radius: 0.625rem;
		}
	}
}

.table-article__row {
	display: flex;
	width: 100%;
	.table-article__cell:first-child {
		font-family: "Manrope-bold";
	}
	&:not(.table-article__row_header) {
		color: $color-zagolovki;
	}

	&:not(.table-article__row_header) .table-article__cell:nth-child(2) {
		font-size: toRem(14);
		color: $color-kontent;
	}

	&:nth-child(even) {
		.table-article__cell {
			background-color: $color-belyy;
			// color: $color-zagolovki;
		}
	}
	&:nth-child(odd) {
		.table-article__cell {
			background-color: $color-seryy-01-2;
		}
		.table-article__cell:first-child {
			border-radius: 0.5rem 0 0 0.5rem;
		}
		.table-article__cell:last-child {
			border-radius: 0 0.5rem 0.5rem 0;
		}
	}
	.table-article__cell:not(:last-child) {
		border-right: 0.0625rem dashed $color-seryy-01-1;
	}
}

.table-article__row_header {
	.table-article__cell {
		font-size: 1.125rem;
		line-height: 150%;
		font-family: "Manrope-bold";
		color: $color-zelenyy-01;
	}
}

.table-article__cell {
	padding: 1.5rem;
	flex: 1;
	text-align: left;
	word-wrap: break-word;
	overflow: hidden;
	line-height: 1.5;
	display: -webkit-box;
	-webkit-line-clamp: 3;
	line-clamp: 3;
	-webkit-box-orient: vertical;
	overflow: hidden;
	// max-height: 6rem;
}

.table-article__cell:first-child {
	flex: 0 0 19.6875rem;
	@media (max-width: $pc) {
	}
}

.table-article__cell:nth-child(2) {
	@media (max-width: $pc) {
		@media (max-width: 47.9375rem) {
			flex: 0 0 32.875rem;
		}
	}
}

.table-article__cell:nth-child(3) {
	flex: 0 0 10rem;
}
