.icon-w20 {
	stroke: $color-zelenyy-01;
	width: 1.25rem;
	height: 1.25rem;
	transition: stroke 0.3s;

	&:hover {
		stroke: $color-seryy-01;
	}
}
.arrow-svg {
	width: 1.5rem;
	height: 1.5rem;
	stroke: $color-seryy-01;
	fill: transparent;
	transition: stroke 0.3s;
}
.dots {
	stroke: $color-zelenyy-01;
	width: 1.5rem;
	height: 1.5rem;
	position: relative;
	transition: stroke 0.3s;
	z-index: 2;
}

button.button {
	cursor: pointer;
	padding: 1rem 1.5rem;
}

.blog {
	&__content {
		@media (min-width: 85.0012rem) {
			display: grid;
			grid-template-columns: auto 20.9375rem;
			gap: toRem(40);
		}
		margin-bottom: toRem(110);
		@media (max-width: $pc) {
			margin-bottom: toRem(80);
			& > *:not(:last-child) {
				margin-bottom: toRem(80);
				@media (max-width: $mobile) {
					margin-bottom: toRem(60);
				}
				@media (max-width: $mobileSmall) {
					margin-bottom: toRem(50);
				}
			}
		}
		@include adaptiveValue("margin-bottom", 70, 50, 0, 768, 380);
	}

	&__comments {
		grid-row: 2/3;
	}
}

.cost-cell {
	position: relative;
	font-size: 1.25rem;
	line-height: 100%;
	color: #f36515;
	font-family: "Unbounded-Bold";
	span {
		.blog & {
			position: absolute;
		}
		font-size: toRem(13);
		font-family: "Manrope1";
	}
}


//  
.blog__aside,
.form-add, h1, h2 {
	opacity: 0;
}