.seo__options.is-active .options__wrap {
	display: block;
}

.seo__options.is-active .options__icon {
	position: absolute;
	right: 1.5938rem;
	transition: all 0.3s;
	transform: rotate(-180deg);
	background-color: #fff;
}

.example-tab {
	margin-top: toRem(70);

	.seo.tabs {
		padding: 0;
	}

	@media (max-width: $tablet) {
		margin-top: toRem(60);
	}

	&__title {
		@include adaptiveValue("font-size", 32, 24, 0, 1360, 380);

		@media (min-width: toEm(380.02)) {
			@include adaptiveValue("margin-bottom", 40, 30, 0, 1360, 768);
		}

		@media (max-width: $mobileXSmall) {
			margin-bottom: toRem(20);
		}

		color: $color-zagolovki;
		font-family: "Unbounded-medium";
	}
}

.options {
	.example-tab & {
		@media (min-width: toEm(380.02)) {
			@include adaptiveValue("margin-bottom", 40, 30, 0, 1360, 768);
		}

		@media (max-width: $mobileXSmall) {
			margin-bottom: toRem(20);
		}

		padding-inline: 0;
	}

	&__item {
		.example-tab[class] & {
			align-items: center;
			margin-bottom: toRem(0);
			gap: toRem(10);
		}
	}

	&__header {
		&.is-active {
			.example-tab & {
				top: 100%;
			}
		}
	}

	&__current {
		.example-tab & {
			br {
				display: none;
			}
		}
	}

	&__wrap {
		.example-tab & {
			& .options__item {
				@media (max-width: $mobileSmall) {
					margin-right: 0;
					padding: 0 0.9375rem 0 0;
					&:not(:last-child) {
						margin-bottom: 0.75rem !important;
					}
					// border-radius: 0;
					// border: 0;
					font-family: "Manrope";
					font-size: 0.9375rem;
					color: #5c6361;
				}
			}
		}
	}

	&__item--select {
		.example-tab[class] &.options__item {
			margin-bottom: toRem(0);
		}
	}
}

.buttons-tabexample {
	&__img {
		img {
			transform: translateY(0.1875rem);
		}
	}

	&__no-active {
		.active & {
			display: none;
		}
	}

	&__active {
		.options__item:not(.active) & {
			display: none;
		}
	}
}

.articles {
	&__panels {
		.example-tab & {
			@media (max-width: $pc) {
				margin-right: -1.9375rem;
			}

			@media (max-width: $tablet) {
				margin-right: -2.4375rem;
			}

			@media (max-width: $mobile) {
				margin-right: -2.0625rem;
			}

			@media (max-width: $mobileSmall) {
				margin-right: -1.25rem;
			}
		}
	}
}

.article {
	.example-tab & {
		img {
			width: 100%;
		}

		padding-top: toRem(0);

		@media (max-width: $mobile) {
			max-height: 25rem;
			overflow: auto;
			scrollbar-width: thin;
			scrollbar-color: $color-zelenyy-01 transparent;

			&::-webkit-scrollbar {
				height: toRem(6);
				width: toRem(6);
			}

			&::-webkit-scrollbar-track {
				border-radius: 0.125rem;
			}

			&::-webkit-scrollbar-thumb {
				background: $color-zelenyy-01;
				border-radius: 0.125rem;
			}
		}
	}
}

.article img {
	.example-tab & {
		margin: 0;

		@media (max-width: $mobile) {
			max-width: initial;
			height: initial;
		}
	}
}
