.notice {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  color: #ffffff;
  overflow: hidden;
  margin-top: -100px;
  padding-top: 80px;
  padding-bottom: 25px;
  background: linear-gradient(140.44deg, #01A587, #036654);
}

.notice::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: linear-gradient(90deg, rgba(196, 247, 86, 0.2), #01A587);
  animation: my-animation 8s linear infinite alternate;
}

.notice::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: linear-gradient(80deg, #036654, rgba(86, 247, 199, 0.3));
  animation: my-animation 8s linear infinite alternate;
}

@keyframes my-animation {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.notice__blur-bg {
  position: absolute;
  background: rgba(86, 247, 131, 0.3);
  z-index: 1;
  animation: move 25s ease-in-out infinite, bgAnimation 35s ease-in-out infinite;
}

.notice__blur-bg2 {
  position: absolute;
  background: rgba(14, 195, 162, 0.5);
  z-index: 1;
  animation: move 35s ease-in-out infinite, bgAnimation 45s ease-in-out infinite;
}

@keyframes bgAnimation {
  0% {
    top: -50%;
    left: -50%;
  }
  25% {
    top: 50%;
    left: 0;
  }
  50% {
    top: 0;
    left: 50%;
  }
  75% {
    left: 0;
    top: -30%;
  }
  100% {
    top: -50%;
    left: -50%;
  }
}
@keyframes move {
  0% {
    border-radius: 200px;
  }
  25% {
    border-radius: 100px 200px 100px 200px;
  }
  50% {
    border-radius: 200px 100px 200px 100px;
  }
  75% {
    border-radius: 50px 200px 50px 200px;
  }
  100% {
    border-radius: 200px;
  }
}
