  outline: none;
}
.form__text:active::placeholder {
  color: #0C0E0D;
}
.form__text--name {
  background-image: url("../img/icon-name.svg");
}
.form__text--name:focus {
  background-image: url("../img/icon-name-active.svg");
}
.form__text--name:active {
  background-image: url("../img/icon-name-active.svg");
}
.form__text--tel {
  background-image: url("../img/icon-tel.svg");
}
.form__text--tel:focus {
  background-image: url("../img/icon-tel-active.svg");
}
.form__text--tel:active {
  background-image: url("../img/icon-tel-active.svg");
}
.form__section--checkbox {
  display: flex;
  align-items: center;
}

.form__label--checkbox {
  font-family: "Manrope-medium";
  font-size: 13px;
  font-weight: 500;
}
.form__label--checkbox a {
  color: #DBFF8E;
}

.form__checkbox {
  display: none;
}

.form__checkbox + .form__label {
  position: relative;
  padding-left: 40px;
}
.form__checkbox + .form__label::before {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  height: 24px;
  width: 24px;
  border-radius: 6px;
  backdrop-filter: blur(25px);
  background: rgba(0, 0, 0, 0.1);
}
.form__checkbox + .form__label::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 7px;
  transform: translateY(-50%);
  width: 11px;
  height: 9px;
  background-size: contain;
  background-image: url("../img/icon-tick-black.svg");
  opacity: 0;
  background-repeat: no-repeat;
}

.form__checkbox:checked + .form__label::before {
  background-color: #C4F756;
}

.form__checkbox:checked + .form__label::after {
  opacity: 1;
}

.form__send.button {
  display: block;
  background: linear-gradient(104.57deg, rgb(196, 247, 86) 16.769%, rgb(158, 212, 42) 99.419%);
  color: #0C0E0D;
  margin-bottom: 30px;
  height: 60px;
  width: 100%;
  font-size: 14px;
  transition: color 0.3s linear, background-color 0.3s linear;
}
.form__send.button:hover {
  background: linear-gradient(108.24deg, rgb(211, 255, 116) 28.511%, rgb(173, 227, 59) 92.257%);
  transition: color 0.3s linear, background-color 0.3s linear;
}
.form__send.button:active {
  background: linear-gradient(108.24deg, rgb(211, 255, 116) 28.511%, rgb(173, 227, 59) 92.257%);
  transition: color 0.3s linear, background-color 0.3s linear;
}

.form__input--error.form__text--name, .form__input--error.form__text--tel {
  outline: 1px solid #d32a34;
}

.form__input--error.form__checkbox + .form__label::before {
  outline: 1px solid #d32a34;
}

.callback-opened .form .form__section {
  opacity: 0;
}

.callback-opened .form .form__send {
  opacity: 0;
}

